<template>
    <section>
        <div class="graph-container">
            <svg class="water-graph" markup-inline   xmlns="http://www.w3.org/2000/svg" width="994" height="768" viewBox="0 0 994 768" id="graph"><g id="layer1"><text x="54" y="84" id="reactor-cooling-text"><tspan x="54" y="84" id="tspan4527">reactor-cooling</tspan></text><text x="70" y="680" id="pump-a-text">pump-a</text><text x="397" y="680" id="pump-b-text">pump-b</text><text x="730" y="680" id="pump-c-text">pump-c</text><g id="pump-a-connection-group"><path id="pump-a-path" d="M170 730v-88c0-16 8-24 24-24h245c16 0 24-8 24-24v-93.255" fill="none" fill-rule="evenodd" stroke="#000" stroke-width="20"/><path :class="{active: state['pump-a'].water.value}" class="pump-connection" d="M170 730v-88c0-16 8-24 24-24h245c16 0 24-8 24-24v-93.255" id="pump-a-connection" fill="none" fill-rule="evenodd" stroke="#fff" stroke-width="10"/><path id="pump-a-flow" d="M170 730v-88c0-16 8-24 24-24h245c16 0 24-8 24-24v-93.255" class="connection-flow" :class="{visible: state['pump-a'].water.value &gt; 0}" fill="none" stroke="#d3d3d3" stroke-width="10" stroke-dasharray="15,15"/></g><path d="M497 726V500.57" id="pump-b-path" fill="none" fill-rule="evenodd" stroke="#000" stroke-width="20"/><path id="pump-c-path" d="M830.462 730v-88c0-16-8-24-24-24h-251c-16 0-24-8-24-24v-92.901" fill="none" fill-rule="evenodd" stroke="#000" stroke-width="20"/><path id="pump-b-connection" d="M497 726V500.57" class="pump-connection" :class="{active: state['pump-b'].water.value}" fill="none" fill-rule="evenodd" stroke="#fff" stroke-width="10"/><path d="M830.462 730v-88c0-16-8-24-24-24h-251c-16 0-24-8-24-24v-92.901" id="pump-c-connection" class="pump-connection" :class="{active: state['pump-c'].water.value}" fill="none" fill-rule="evenodd" stroke="#fff" stroke-width="10"/><text x="280" y="380" id="text4510">{{ $t(&apos;cooling.water&apos;) }}</text><text x="397" y="448" id="water-tank-text">water-tank</text><g id="reactor-cooling-connection-group"><path id="reactor-cooling-path" d="M479.935 500.939v-82c-.057-16.043-8.337-24.252-24-24h-262.5c-15.958.031-24-8-24-24v-268" fill="none" fill-rule="evenodd" stroke="#000" stroke-width="20"/><path :class="{active: state['reactor-cooling'].waterSatisfaction.value &gt; 0 &amp;&amp; state['reactor-cooling'].waterRequired.value &gt; 0}" class="tank-connection" d="M479.935 500.939v-82c0-16-8-24-24-24h-262.5c-16 0-24-8-24-24v-268" id="reactor-cooling-connection" fill="none" fill-rule="evenodd" stroke="#fff" stroke-width="10"/><path id="reactor-cooling-flow" d="M479.935 500.939v-82c-.057-16.043-8.337-24.252-24-24h-262.5c-15.958.031-24-8-24-24v-268" class="connection-flow" :class="{visible: state['reactor-cooling'].waterSatisfaction.value &gt; 0 &amp;&amp; state['reactor-cooling'].waterRequired.value &gt; 0}" fill="none" stroke="#d3d3d3" stroke-width="10" stroke-dasharray="15,15"/></g><path :class="{visible: state['pump-b'].water.value}" class="connection-flow" d="M497 726V500.57" id="path4545" fill="none" stroke="#d3d3d3" stroke-width="10" stroke-dasharray="15,15"/><path :class="{visible: state['pump-c'].water.value}" class="connection-flow" id="path4548" d="M830.462 730v-88c0-16-8-24-24-24h-251c-16 0-24-8-24-24v-92.901" fill="none" stroke="#d3d3d3" stroke-width="10" stroke-dasharray="15,15"/><path d="M865 331.939v-179c-.057-16.043-8.337-24.252-24-24H665" id="base-path" fill="none" fill-rule="evenodd" stroke="#000" stroke-width="20"/><path id="water-treatment-path" d="M514.685 500.939v-348c.057-16.043 8.337-24.252 24-24h59.5" fill="none" fill-rule="evenodd" stroke="#000" stroke-width="20"/><path d="M514.685 500.939v-348c.057-16.043 8.337-24.252 24-24h59.5" id="water-treatment-connection" class="tank-connection" :class="{active: state['water-treatment'].requiredWater.value &gt; 0}" fill="none" fill-rule="evenodd" stroke="#fff" stroke-width="10"/><path id="water-treatment-flow" d="M514.685 500.939v-348c.057-16.043 8.337-24.252 24-24h59.5" class="connection-flow" :class="{visible: state['water-treatment'].requiredWater.value &gt; 0}" fill="none" stroke="#d3d3d3" stroke-width="10" stroke-dasharray="15,15"/><text id="base-text" y="331" x="765">base</text><text x="565" y="84" id="water-treatment-text">water-treatment</text><path id="base-connection" d="M865 331.939v-179c-.057-16.043-8.337-24.252-24-24H665" :class="{active: state['base'].drinkingWaterSatisfaction.value &gt; 0}" class="tank-connection" fill="none" fill-rule="evenodd" stroke="#fff" stroke-width="10"/><path class="connection-flow reverse" :class="{visible: state['base'].drinkingWaterSatisfaction.value &gt; 0}" d="M865 331.939v-179c-.057-16.043-8.337-24.252-24-24H665" id="base-flow" fill="none" fill-rule="evenodd" stroke="#fff" stroke-width="10"/></g></svg></svg>

            <div class="graph-overlay" data-target="reactor-cooling-text">
                <h3>{{ $t('stateMachine.reactor-cooling.name') }}</h3>
                {{ $t('water.litersPerHour', {amount: Math.round(state['reactor-cooling'].waterRequired.value * 3600)})}}
            </div>

            <div class="graph-overlay" data-target="water-treatment-text">
                <h3>{{ $t('stateMachine.water-treatment.name') }}</h3>
                {{ $t('power.kilowattHours', {power: Math.round((state['water-treatment'].requiredPower.value * state['water-treatment'].powerSatisfaction.value) * 10) / 10})}}
                /
                {{ $t('power.kilowattHours', {power: Math.round(state['water-treatment'].requiredPower.value * 10) / 10})
                }}
                <br>
                {{ $t('water.litersPerHour', {amount: Math.round(state['water-treatment'].requiredWater.value * 3600)})}}
                <br>
                {{ $t('water.drinking') }}: {{ Math.round(state['water-treatment'].drinkingWater.value) }} L
                <br>
                <br>
                cleaner: {{ state['water-treatment'].resourceCleaner.status.id }}
                <br>
                chlorine: {{ state['water-treatment'].resourceChlorine.status.id }}
                <br>
                minerals: {{ state['water-treatment'].resourceMinerals.status.id }}
            </div>

            <div class="graph-overlay" data-target="base-text">
                <h3>{{ $t('stateMachine.base.name') }}</h3>
                power: {{ Math.round(state['base'].powerSatisfaction.value * 100) }}%
                <br>
                water: {{ Math.round(state['base'].drinkingWaterSatisfaction.value * 100) }}%
            </div>

            <div class="graph-overlay" data-target="water-tank-text">
                <h3>{{ $t('stateMachine.water-tank.name') }} <span class="small">{{ $t('water.industrial') }}</span>
                </h3>
                <div class="block-group">
                    <div class="block" style="width: 80%;">
                        {{ Math.round(state['water-tank'].water.value / 100) / 10 }} m³
                        /
                        {{ Math.round(state['water-tank'].capacity.value / 100) / 10 }} m³
                        <br>
                        {{ $t('water.litersPerHour', {amount: Math.round((totalWaterProduction - totalWaterConsumption) * 3600)})}}
                    </div>

                    <div class="block" style="width: 20%;">
                        <gauge type="linear" :options="waterTankGaugeOptions"
                               :value="state['water-tank'].water.value / state['water-tank'].capacity.value"></gauge>
                    </div>
                </div>
            </div>

            <div class="graph-overlay" v-for="pump in pumps" :data-target="pump + '-text'">
                <h3>{{ $t('stateMachine.' + pump + '.name') }}</h3>
                <button v-if="!state[pump].enabled.value" @click="() => changeProperty(pump, 'enabled', 1)">
                    enable
                </button>
                <button v-else @click="() => changeProperty(pump, 'enabled', 0)">disable</button>
                <br>
                filter: {{ Math.round((state[pump].filterHealth.value / state[pump].filterMaxHealth.value) * 100) }}%
            </div>
        </div>
    </section>
</template>

<style lang="less" scoped>
    @import "../../../assets/less/engineering_variables";

    .graph-container {
        position: relative;

        width: 944px;
        height: 768px;
    }

    @keyframes connection-flow {
        to {
            stroke-dashoffset: -30;
        }
    }

    @keyframes connection-flow-reverse {
        to {
            stroke-dashoffset: 30;
        }
    }

    .water-graph {
        position: absolute;

        text {
            fill: @text-color;
        }

        .pump-connection, .tank-connection {
            stroke: @signal-blue-disabled;
            transition: stroke 0.5s;

            &.active {
                stroke: @signal-blue;
            }
        }

        .connection-flow {
            /* Animate dashes */
            stroke-dasharray: 15;
            animation: connection-flow 1s linear;
            animation-iteration-count: infinite;
            &.reverse {
                animation: connection-flow-reverse 1s linear;
                animation-iteration-count: infinite;
            }

            stroke: @signal-blue-disabled;
            transition: stroke 0.5s;

            &.visible {
                stroke: @signal-blue-highlight;
                transition: stroke 1s;
            }
        }
    }

    .graph-overlay {
        padding: 8px 10px;

        position: absolute;

        width: 200px;
        overflow: hidden;

        background-color: @label-background;

        h1, h2, h3, h4, h5, h6 {
            margin: 0 0 0.5em;

            .small {
                font-size: 0.75em;
                color: @text-unused-color;
            }
        }
    }
</style>

<script>
    import EngineeringMixin from '../../../mixins/engineering';

    import SevenSegmentDisplay from '../../controls/SevenSegmentDisplay';
    import Gauge from '../../controls/Gauge';

    export default {
        name: 'water',
        mixins: [EngineeringMixin],
        data() {
            return {
                pumps: ['pump-a', 'pump-b', 'pump-c'],
                waterTankGaugeOptions: {
                    minValue: 0,
                    maxValue: 1,
                    majorTicks: [0, 0.25, 0.5, 0.75, 1],
                    minorTicks: 1,
                    strokeTicks: true,
                    numbersMargin: 0,
                    barWidth: 50,
                    height: 100,
                    animation: false,
                    units: false,
                    title: false,
                    animationRule: 'linear',
                    colorPlate: 'transparent',
                    colorMajorTicks: '#ebebeb', // @text-color
                    colorNeedle: '#ff4629', // @signal-red-highlight
                    colorNeedleEnd: false,
                    colorBar: '#6f6f6f', // @signal-blue-disabled
                    colorBarProgress: '#8ea7ff', // @signal-blue-highlight
                    colorNumbers: 'transparent',
                    needle: false,
                    borders: false,
                    valueBox: false,
                    tickSide: 'left',
                    ticksWidth: 15,
                    ticksPadding: 15,
                    numberSide: 'left',
                    barBeginCircle: false,
                    barLength: 100,
                },
            };
        },
        mounted() {
            document.querySelectorAll('.graph-container .graph-overlay').forEach(element => {
                const targetId = element.getAttribute('data-target');
                if (!targetId) {
                    throw new Error('No target set on element');
                }
                const target = document.getElementById(targetId);
                if (!target) {
                    throw new Error(`Target element ${targetId} does not exist`);
                }

                const box = target.getBBox();

                element.style.left = box.x + 'px';
                element.style.top = box.y + 'px';

                target.textContent = '';
            });
        },
        computed: {
            totalWaterConsumption() {
                return this.state['reactor-cooling'].waterRequired.value + this.state['water-treatment'].requiredWater.value;
            },
            totalWaterProduction() {
                return this.pumps.map(pump => this.state[pump].water.value).reduce((total, value) => total + value, 0);
            },
        },
        components: {
            SevenSegmentDisplay,
            Gauge,
        },
    };
</script>



